<template>
  <div>
    <v-container>
      <title-ribbon v-bind="{ title: `Orientación`, sucursal }" />
      <v-row class="form-orientacion__row">
        <v-col class="col-12 col-md-3 ">
          <v-row>
            <v-col cols="12 text-center text-md-left">
              <siguenos />
            </v-col>
          </v-row>
        </v-col>
        <v-col class="col-12 col-md-9 ">
          <div class="form-contactanos__box">
            <validation-observer
              ref="observer"
              v-slot="{ invalid }"
            >
              <slide-x-left-transition>
                <form
                  class="px-0 mx-0 my-0"
                  @submit.prevent="validateForm($route.params.sucursal)"
                >
                  <v-card style="margin: 0px;">
                    <v-row class="d-flex justify-center">
                      <v-col class="col-12 col-lg-6">
                        <v-row>
                          <v-col>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="required|max:100"
                              name="Nombres"
                            >
                              <label class="grey--text">
                                Nombres
                              </label>
                              <v-text-field
                                v-model="nombres"
                                :error-messages="errors"
                                color="success"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="required|max:100"
                              name="Apellidos"
                            >
                              <label class="grey--text">
                                Apellidos
                              </label>
                              <v-text-field
                                v-model="apellidos"
                                :error-messages="errors"
                                color="success"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="required|numeric|max:50"
                              name="Celular o Teléfono"
                            >
                              <label class="grey--text">
                                Celular o Teléfono
                              </label>
                              <v-text-field
                                v-model="celular"
                                :error-messages="errors"
                                color="success"
                                type="number"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              name="date"
                              rules="required"
                            >
                              <v-menu
                                ref="menuFnac"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                transition="zoom-center-transition"
                                offset-y
                                min-width="auto"
                              >
                                <template v-slot:activator="{ on }">
                                  <label class="grey--text">
                                    Fecha de Nacimiento
                                  </label>
                                  <v-text-field
                                    v-model="computedOrdenarFecha"
                                    label="Fecha de Nacimiento"
                                    color="success"
                                    name="date"
                                    readonly
                                    :error-messages="errors[0]"
                                    filled
                                    dense
                                    v-on="on"
                                  />
                                </template>
                                <v-date-picker
                                  v-model="fechaNacimiento"
                                  locale="es-ES"
                                  color="success"
                                  @input="menuFnac = false"
                                />
                              </v-menu>
                            </validation-provider>
                          </v-col>
                        </v-row>
                      </v-col>

                      <v-col class="col-12 col-lg-6">
                        <v-row>
                          <v-col>
                            <div>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="rut"
                                name="Rut"
                              >
                                <label class="grey--text">
                                  Rut
                                </label>
                                <v-text-field
                                  v-model="rut"
                                  :error-messages="errors"
                                  color="success"
                                  filled
                                  dense
                                  :validate-on-blur="false"
                                  name="user[rut]"
                                  flat
                                />
                              </validation-provider>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="required|email|max:100"
                                name="E-mail"
                              >
                                <label class="grey--text">
                                  E-mail
                                </label>
                                <v-text-field
                                  v-model="email"
                                  :error-messages="errors"
                                  color="success"
                                  type="text"
                                  filled
                                  dense
                                />
                              </validation-provider>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="required|max:50"
                                name="Previsión"
                              >
                                <label class="grey--text">
                                  Previsión
                                </label>
                                <v-select
                                  v-model="prevision"
                                  :error-messages="errors"
                                  color="success"
                                  :items="previsionesTodas"
                                  item-text="Nombre"
                                  :menu-props="{ maxHeight: '400' }"
                                  label="Seleccione previsión"
                                  persistent-hint
                                  filled
                                  dense
                                />
                              </validation-provider>
                            </div>
                            <div>
                              <fade-transition>
                                <div style="height: 57px;">
                                  <v-alert
                                    v-show="showRecaptchaAlert"
                                    dense
                                    text
                                    type="warning"
                                    style="padding: 1px; margin: 0px;"
                                    class="box"
                                  >
                                    Marque la casilla de verificación
                                  </v-alert>
                                  <v-alert
                                    v-show="showEnviado"
                                    dense
                                    text
                                    type="success"
                                    style="padding: 1px; margin: 0px;"
                                    class="box"
                                  >
                                    Formulario Enviado
                                  </v-alert>
                                </div>
                              </fade-transition>
                              <!-- testing -->
                              <!-- <vue-recaptcha
                                sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                                @verify="onVerify"
                                @expired="onExpired"
                              /> -->
                              <!-- production -->
                              <vue-recaptcha
                                sitekey="6Lf7HQQeAAAAAC7MOA15SB35MQ8UwUh8wYG5rg-h"
                                @verify="onVerify"
                                @expired="onExpired"
                              />
                              <!-- mobile -->
                              <!-- <vue-recaptcha
                          sitekey="6Le-Bu8bAAAAAOIi7Am_cyMl4OqY0rn8fg7UI9ry"
                          @verify="onVerify"
                          @expired="onExpired"
                        /> -->
                              <div class="form-buttons py-5">
                                <v-btn
                                  color="success"
                                  default
                                  type="submit"
                                  :disabled="invalid"
                                  :loading="btnLoader"
                                >
                                  Enviar
                                </v-btn>
                              </div>
                            </div>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>
                  </v-card>
                </form>
              </slide-x-left-transition>
            </validation-observer>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  import VueRecaptcha from 'vue-recaptcha'
  import { SlideXLeftTransition, FadeTransition } from 'vue2-transitions'
  import { mapState } from 'vuex'
  // import { extend } from 'vee-validate'

  export default {
    name: 'OrientacionFormulario',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Siguenos: () => import('@/components/web/base/Siguenos'),
      VueRecaptcha,
      SlideXLeftTransition,
      FadeTransition,
    },
    data: () => ({
      sucursal: '',
      nombres: '',
      apellidos: '',
      rut: '',
      email: '',
      celular: '',
      prevision: '',
      fechaNacimiento: '',
      sucursalNombre: '',
      recaptchaVerified: false,
      showRecaptchaAlert: false,
      showEnviado: false,
      btnLoader: false,
      menuFnac: false,
      previsionesTodas: [],
    // date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
    //   .toISOString()
    //   .substr(0, 10),
    }),
    computed: {
      ...mapState('web', ['sucursales']),
      computedOrdenarFecha () {
        return this.ordenarFecha(this.fechaNacimiento)
      },
    },
    watch: {
      recaptchaVerified: function () {
        if (this.recaptchaVerified) {
          this.hideMsg()
        }
      },
      rut: function () {
        this.rut = this.rut.replace(/^0+|[^0-9kK]+/g, '').toUpperCase()
        const len = this.rut.length - 1
        if (len > 3) {
          const dv = this.rut[len]
          const beforeDv = this.rut
            .substring(0, len)
            .replace(/\B(?=(\d{3})+(?!\d))/g, '.')
          this.rut = beforeDv + '-' + dv
        }
      },
    },
    created () {
      // this.test()
      this.checkSucursal()
      this.getPrevisionesTodas()
    },
    methods: {
      async validateForm () {
        const isValid = await this.$refs.observer.validate()
        if (!isValid) {
          return false
        }

        if (!this.recaptchaVerified) {
          this.showRecaptchaAlert = true
          return false
        }

        console.log('validando')
        this.btnLoader = true

        const rutCompleto = this.rut
          .split('.')
          .join('')
          .trim()

        const [rut, digito] = rutCompleto.split('-')

        const body = {
          sucursal: this.sucursal,
          nombres: this.nombres,
          apellidos: this.apellidos,
          rut: rut,
          digito: digito,
          email: this.email,
          celular: this.celular,
          prevision: this.prevision,
          fechaNacimiento: this.fechaNacimiento,
        }

        Vue.axios
          .post('/api/formularios/orientacion', body)
          .then(response => {
            // console.log(response)
            this.showEnviadoMsg()
            this.limpiarFormulario()
            this.btnLoader = false
          })
          .catch(error => {
            console.log(error.message)
          })
      },
      onVerify () {
        if (!this.recaptchaVerified) {
          this.recaptchaVerified = true
        }
      },
      onExpired () {
        this.recaptchaVerified = false
      },
      hideMsg () {
        this.showRecaptchaAlert = false
      },
      showEnviadoMsg () {
        this.showEnviado = true
        setTimeout(() => {
          this.showEnviado = false
        }, 10000)
      },
      limpiarFormulario () {
        this.nombres = ''
        this.apellidos = ''
        this.rut = ''
        this.fechaNacimiento = ''
        this.prevision = ''
        this.celular = ''
        this.email = ''
        requestAnimationFrame(() => {
          // Reseteamos vee
          this.$refs.observer.reset()
        })
        this.onExpired() // Reseteamos captcha
        this.paso1 = true
        this.paso2 = false
      },
      ordenarFecha (fecha) {
        if (!fecha) return null

        const [year, month, day] = fecha.split('-')
        return `${day}/${month}/${year}`
      },
      checkSucursal () {
        const buscaSucursalRoute = this.sucursales.find(
          suc => suc.CodSucursal === Number(this.$route.params.sucursal),
        )
        this.sucursal = buscaSucursalRoute
          ? buscaSucursalRoute.CodSucursal
          : this.$router.push('/orientacion-psicologica')
      },
      getPrevisionesTodas () {
        Vue.axios
          .get('/api/previsiones/todas')
          .then(response => {
            this.previsionesTodas = response.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      test () {
        // console.log(
        //   new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        //     .toISOString()
        //     .substr(0, 10),
        // )
        console.log(this.previsiones)
      },
    },
  }
</script>
